.message{width:300px;text-align:center}.upload{--background: #FFFFFF;--text-drop: #CAC8CA;--text-upload: #2D2C2F;--progress-color: #E7E5E7;--check-color: var(--text-upload);--border-radius: 30px;width:300px;height:300px;position:relative;display:flex;justify-content:center;align-items:center}.upload::before{content:"";display:block;width:100%;height:100%;background:var(--background);border-radius:var(--border-radius);transition:all .3s ease-out;box-shadow:0px 1px 3px rgba(0,0,0,0.1);box-shadow:var(--shadow-x, 0px) var(--shadow-y, 1px) var(--shadow-blur, 3px) rgba(0,0,0,0.1);transform:scale(1);transform:scale(var(--scale, 1))}.upload .drop-here{position:absolute;width:100%;height:100%;outline:none;border-radius:var(--border-radius);opacity:0;opacity:var(--opacity, 0);overflow:hidden;cursor:pointer;text-indent:-9999px;z-index:1}.upload .text{position:absolute;font-size:21px;text-transform:uppercase;letter-spacing:2px;font-weight:bold}.upload .text.text-drop{color:var(--text-drop);opacity:1;opacity:var(--opacity, 1);transition:opacity .15s ease-out .15s}.upload .text.text-upload{color:var(--text-upload);opacity:0;opacity:var(--opacity, 0);transition:opacity .15s ease-out}.upload .progress-wrapper{position:absolute}.upload .progress-wrapper .progress{fill:none;stroke:var(--progress-color);stroke-width:3;stroke-dasharray:722;stroke-dashoffset:722}.upload .check-wrapper{position:absolute;opacity:0;opacity:var(--opacity, 0);transform:scale(0.9) rotate(3deg);transform:scale(var(--scale, 0.9)) rotate(var(--rotate, 3deg));transition:transform .15s ease-in-out, opacity .15s ease-in}.upload .check-wrapper .check{width:100px;width:100px;fill:none;stroke:var(--check-color);stroke-width:7;stroke-linecap:round;stroke-linejoin:round;stroke-dasharray:100 0;stroke-dashoffset:100}.upload .shadow{opacity:0;opacity:var(--opacity, 0);overflow:hidden;position:absolute;height:100%;width:100%;border-radius:var(--border-radius);-webkit-filter:blur(25px);filter:blur(25px);z-index:-1;transition:all .5s ease}.upload .shadow::before{content:'';position:absolute;top:-25%;left:-25%;height:150%;width:150%;background:conic-gradient(#4b82d4, #6a29e9, #4b82d4, #6a29e9, #c58357, #FFC940, #4b82d4);animation:shadow-animate 5s linear infinite}.upload.drag{--scale: 1.03;--shadow-y: 5px;--shadow-blur: 20px}.upload.drop .text.text-drop{--opacity: 0;transition:opacity .15s ease-out}.upload.drop .text.text-upload{--opacity: 1;transition:opacity .15s ease-out .15s}.upload.drop .shadow{--opacity: 1}.upload.drop .progress-wrapper{opacity:1;opacity:var(--opacity, 1);transform:scale(1) rotate(-90deg);transform:scale(var(--scale, 1)) rotate(var(--rotate, -90deg))}.upload.drop .progress-wrapper .progress{animation:progress-animate 3s ease .3s forwards}.upload.done{--opacity: 0}.upload.done .text.text-upload{--opacity: 0}.upload.done .shadow{--opacity: 0}.upload.done .progress-wrapper{--scale: .95;transition:transform .3s, opacity .3s}.upload.done .check-wrapper{--opacity: 1;--scale: 1;--rotate: 0deg;transition:transform .5s ease-in-out .3s, opacity .5s ease-in .3s}.upload.done .check-wrapper .check{animation:check-animate .5s ease-in-out .3s forwards}@keyframes shadow-animate{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes progress-animate{0%{stroke-dashoffset:722}20%{stroke-dashoffset:500}50%{stroke-dashoffset:322}55%{stroke-dashoffset:300}100%{stroke-dashoffset:0}}@keyframes check-animate{0%{stroke-dasharray:0 100;stroke-dashoffset:0}100%{stroke-dasharray:100 0;stroke-dashoffset:100}}body{background:#E8EBF3;height:100vh;font-family:Arial, Helvetica, sans-serif;font-size:16px;display:flex;flex-direction:column;justify-content:center;align-items:center}

:root {
  --condiv-flex: 6;
  --fontsize-p: 1rem;
}

* {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  width: 100%;
}

body {
  font-size: 16px;
  font-family: 'Arial', Helvetica, sans-serif;
}

.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

.mobileCenter {
  display: flex;
  justify-content: center;
}

a {
  text-decoration: none;
  color: #000;
}

.subtopic {
  margin: 10px 10px 30px 10px !important;
}

p {
  font-size: 1rem;
  font-size: var(--fontsize-p);
}

h1, h2, h3, h4, p {
  margin: 10px;
}

.card {
  padding: 30px;
  margin-bottom: 20px;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.card:hover {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.active {
  font-weight: bolder;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav: 1rem;
    --sidebar-flex: 0;
    --typing-font: 2rem;
  }

  .App {
    flex-direction: column;
  }

  .sidebar {
    padding: 30px;
  }

  .condiv {
    flex: 0 1;
    padding: 15px;
  }
}

